* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  border: 1px solid #0d161b;
  border-radius: 5px;
  display: block;
}

button {
  width: 140px;
  height: 40px;
  cursor: pointer;
  background-color: #eff160;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.gallery-container {
  padding: 60px 0;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.load-more {
  width: 200px;
  background-color: #6067f1;
  margin: 40px auto;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: 0 auto;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
